body {
  font-family: 'Helvetica Neue';
  font-weight: 200;
}

.card {
  box-shadow: 1px solid black;
  height: 150px;
  width: 150px;
}

.nav>li>a>img {
  border-radius: 50px;
  height: 50px;
}

.spacer {
  margin-bottom: 100px;
}

.cart-image {
  width: 100px;
}

.table>tbody>tr>td {
  vertical-align: middle;
}

.side-by-side {
  display: inline-block;
}

.navbar-nav>li>a {
  padding-left: 5px;
  padding-right: 15px;
  padding-top: 15px;
  padding-bottom: 5px;
}
